

.setting-header{
    padding: 0rem 1.5rem;
    display: grid;
    grid-template-columns: max-content 1fr;
    column-gap: .8rem;
    align-items: center;
    margin-bottom: 1rem;
}

.user-icon{
    display: flex;
justify-content: center;
align-items: center;
grid-row: 1 / 3;
}

.user-icon i{

    font-size: 2rem;
background: var(--light-color);
width: 50px;
height: 50px;
display: flex;
justify-content: center;
align-items: center;
border-radius: 50%;
border: 1px solid var(--alt-text-color);

}

.user-full-name{
font-weight: 600;
}

.user-settings{
    margin-top: 1rem;
    min-width: 350px;
}

.user-log-out button{
    width: 100%;
    margin-top: 1rem;
}


.user-email{
    grid-column: 2;
    font-size: 1rem;
}
.update-user-list li{
    color: var(--alt-text-color);
    padding: .9rem 1.5rem;
    transition: .2s;
    border-bottom: 1px solid var(--light-silver);
}

.user-log-out{
    padding: .9rem 1.5rem;
}

.update-user-list li:hover{
    background: var(--light-color);
}